import { createStore } from 'vuex'
import utils from '../plugins/utils'

export default createStore({
  state: {
    coupons: {}
  },
  getters: {
    getCoupons(state) {      
      return state.coupons
    }
  },
  mutations: {
    setCoupons(state, coupons) {
      state.coupons = coupons
    },
    updateCoupon(state, coupon) {
      state.coupons = Object.assign({},state.coupons, coupon)      
    }
  },
  actions: {
    loadCoupons({commit}) {
      let savedCoupons = localStorage.getItem('coupons') || '{}'
      savedCoupons = JSON.parse(savedCoupons)
      commit('setCoupons', savedCoupons)
    },
    appendCoupon({commit, dispatch}, couponNo) {
      let savedCoupons = localStorage.getItem('coupons') || '{}'
      savedCoupons = JSON.parse(savedCoupons)
      if(savedCoupons.hasOwnProperty(couponNo)){
        return {success: false, message: '이미 등록된 쿠폰입니다'}
      }
      savedCoupons[couponNo] = {
        id: couponNo,
        count: 1,
        created_at: new Date().getTime()
      }
      localStorage.setItem('coupons', JSON.stringify(savedCoupons))
      dispatch('loadCoupons')
      return {success: true}
    },
    saveCoupons({state, dispatch}) {
      let coupons = state.coupons
      localStorage.setItem('coupons', JSON.stringify(coupons))
      dispatch('loadCoupons')
    }
  },
  modules: {
  }
})
