<template>
  <transition name="modal">
    <div class="modal-mask bg-white d-flex align-items-center justify-content-center">
      <div>
        <h1 style="font-size: 48px;" class="mb-5">Mission Complete!</h1>
        <div class="text-center">
          <button type="button" class="btn btn-lg" @click="onClickComplete()">
            <i class="mdi mdi-check"></i>
            다시 시작하기
          </button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'CompleteModal',    
  methods: {
    startConfetti() {
      this.$confetti.start({
        particles: [
          {
            type: 'heart',            
          },
          {
            type: 'circle',            
          }          
        ],
        defaultDropRate: 5,
        defaultSize: 5,
      });
    },
    onClickComplete() {
      this.$confetti.stop()
      this.$emit('on-complete')
    }
  },
  mounted () {
    window.addEventListener('keydown', this.escCloseListener)
    document.documentElement.style.overflowY = 'hidden'
    this.startConfetti()
  },
  destroyed () {
    window.removeEventListener('keydown', this.escCloseListener, false)
    document.documentElement.style.overflowY = 'auto'
  },   
}
</script>


<style lang="scss" scoped>  
.modal-mask{
  z-index: 0;
  .modal-container{
    width: 100%;
    height: 500px;
  }
}
  
</style>