<template>
  <transition name="modal">
    <div class="modal-mask d-flex justify-content-center align-items-center">
      <div>
        <div ref="dice" class="dice" :class="[(diceNum > 0) ? `show-${diceNum}` : '']">
          <div class='side one'>
            <div class="dot one-1"></div>
          </div>
          <div class='side two'>
            <div class="dot two-1"></div>
            <div class="dot two-2"></div>
          </div>
          <div class='side three'>
            <div class="dot three-1"></div>
            <div class="dot three-2"></div>
            <div class="dot three-3"></div>
          </div>
          <div class='side four'>
            <div class="dot four-1"></div>
            <div class="dot four-2"></div>
            <div class="dot four-3"></div>
            <div class="dot four-4"></div>
          </div>
          <div class='side five'>
            <div class="dot five-1"></div>
            <div class="dot five-2"></div>
            <div class="dot five-3"></div>
            <div class="dot five-4"></div>
            <div class="dot five-5"></div>
          </div>
          <div class='side six'>
            <div class="dot six-1"></div>
            <div class="dot six-2"></div>
            <div class="dot six-3"></div>
            <div class="dot six-4"></div>
            <div class="dot six-5"></div>
            <div class="dot six-6"></div>
          </div>
        </div>        
      </div>       
    </div>
  </transition>
</template>

<script>
  export default {
    name: 'DiceModal',
    props: {
      couponNo: {
        type: String,
        default: null
      }
    },
    data() {
      return {        
        diceNum: -1        
      }
    },
    methods: {
      
    },
    mounted() {
      document.documentElement.style.overflowY = 'hidden'
      this.$store.dispatch('loadCoupons')
      const targetCoupon = this.getCoupons[this.couponNo]
      window.addEventListener('keydown', this.escCloseListener)      
      setTimeout(()=>{        
        const diceResult = Math.floor((Math.random() * 6) + 1)        
        this.diceNum = diceResult        
        targetCoupon.count = 0   
        const updateCoupon = {}
        updateCoupon[this.couponNo] = targetCoupon
        this.$store.commit('updateCoupon', updateCoupon)
        this.$store.dispatch('saveCoupons')
        setTimeout(()=>{          
          this.$emit('on-complete', diceResult)
        }, 2000)
      }, 1000)   
    },
    methods: {
      
    },
    destroyed() {
      window.removeEventListener('keydown', this.escCloseListener, false)
      document.documentElement.style.overflowY = 'auto'
    },
    computed: {
      getCoupons() {
        return this.$store.getters['getCoupons']
      }
    }
  }
</script>


