<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header d-block">
            <div class="row">
              <div class="col-9 col-form-label">
                <i class="mdi mdi-ticket"></i>
                내 쿠폰함
              </div>
              <div class="col-3 text-right">
                <a href="javascript:;" class="btn btn-sm" @click="$emit('on-close')">
                  <i class="mdi mdi-close"></i>
                </a>
              </div>
            </div>
          </div>
          <div class="modal-body">
            <p>
              <small class="text-danger" v-if="inputErrMsg">
                <i class="mdi mdi-information"></i>
                {{ inputErrMsg }}
              </small>
            </p>
            <div class="input-group mb-3">
              <input type="search" class="form-control text-center underline" ref="inputCouponNo" placeholder="8자리 쿠폰번호를 입력해주세요" maxlength="8" @keyup.enter="onClickAdd()">
              <div class="input-group-append">
                <button class="btn" type="button" @click="onClickAdd()">
                  <i class="mdi mdi-plus"></i> 등록
                </button>
              </div>
            </div>            
            <hr>
            <div style="height: 300px; overflow: auto;">
              <div class="card mb-1" v-for="(item, index) of getCoupons" :key="index">
                <div class="card-body pt-1 pb-1">
                  <div class="row">
                    <div class="col-9 col-form-label">
                      <span class="mr-1" :class="{'coupon-disabled': item.count == 0}">{{index + 1}}) {{ item.id }}</span>
                      <span v-if="item.count == 0" class="badge badge-secondary">
                        사용함
                      </span>                      
                    </div>
                    <div class="col-3 text-right">
                      <button type="button" v-if="item.count == 0" class="btn btn-sm" @click="onClickDeleteCoupon(item)">
                        <i class="mdi mdi-close"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>          
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'AddCouponModal',  
  props: {    
    
  },
  data() {
    return {
      inputErrMsg: null
    }
  },
  methods: {
    escCloseListener(evt) {           
      if(evt.key != 'Escape') return false
      this.$emit('on-close')
    },
    async onClickAdd() {
      this.inputErrMsg = null
      const value = this.$refs.inputCouponNo.value
      if(value == ''){
        this.inputErrMsg = '쿠폰번호를 입력해 주세요'
        this.$refs.inputCouponNo.focus()
        return 
      }
      let pattern = /[0-9]{8}/g
      if(!pattern.test(value)){
        this.inputErrMsg = '쿠폰번호는 8자리의 숫자로 입력해 주세요'
        this.$refs.inputCouponNo.focus()
        return 
      }      
      let appendResult = await this.$store.dispatch('appendCoupon', value)      
      if(!appendResult.success) {
        this.inputErrMsg = appendResult.message
        this.$refs.inputCouponNo.focus()
        return
      }
      this.$refs.inputCouponNo.value = ''
      this.$refs.inputCouponNo.focus()
    },
    onClickDeleteCoupon(coupon) {
      let coupons = this.$store.getters['getCoupons']
      delete coupons[coupon.id]
      this.$store.commit('setCoupons', coupons)
      this.$store.dispatch('saveCoupons')
    }
  },
  mounted () {
    this.$store.dispatch('loadCoupons')
    window.addEventListener('keydown', this.escCloseListener)
    document.documentElement.style.overflowY = 'hidden'
    this.$refs.inputCouponNo.focus()
  },
  destroyed () {
    window.removeEventListener('keydown', this.escCloseListener, false)
    document.documentElement.style.overflowY = 'auto'
  },
  computed: {
    getCoupons() {
      let coupons = this.$store.getters['getCoupons']
      coupons = Object.values(coupons)
      coupons.sort((a,b)=>(a.count < b.count) ? 1 : -1)
      return coupons
    }
  }
}
</script>


<style lang="scss" scoped>  
  .modal-container{
    max-width: 480px;
    margin-top: 100px;
    .coupon-disabled{
      color: #33333380;
      text-decoration: line-through;
    }
  }
</style>