<template>
  <div class="home-index-page" :style="{height: `${screenHeight}px`}">
    <nav class="navbar navbar-expand-lg navbar-light bg-light mb-3">
      <div class="container">
        <a class="navbar-brand text-primary" href="#">
          <i class="mdi mdi-dice-4"></i>
          주사위 놀이
        </a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <a class="nav-link" href="javascript:;" @click="openAddCoupon = !openAddCoupon">
                <i class="mdi mdi-plus"></i> 쿠폰번호 등록
              </a>
            </li>            
            <li class="nav-item">
              <a class="nav-link" href="javascript:;" @click="onClickPlayDice()">
                <i class="mdi mdi-dice-multiple-outline"></i> 주사위 던지기
                <span class="badge badge-danger">
                  {{ getAvailableDiceCnt }}
                </span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <div class="container">
      <div class="d-flex justify-content-center">
        <canvas id="canvas" ref="canvas"></canvas>
      </div>
    </div>
  </div>
  <dice-modal v-if="useCouponNo" :coupon-no="useCouponNo" @on-complete="(diceResult)=>{onDicePlayCompleted(diceResult); useCouponNo=null}"/>
  <add-coupon-modal v-if="openAddCoupon" @on-close="openAddCoupon=false"/>
  <alert-modal v-if="alertMsg" :msg="alertMsg" @on-confirm="alertMsg=null"/>
  <complete-modal v-if="isMissionComplete" @on-complete="onCompleteMission()"/>
</template>

<script>
import CompleteModal from '@/components/CompleteModal.vue'
import AlertModal from '@/components/AlertModal.vue'
import AddCouponModal from '@/components/AddCouponModal.vue'
import DiceModal from '@/components/DiceModal.vue'
import { fabric } from 'fabric'
export default {
  name: 'HomeIndexPage',
  components: {
    DiceModal,
    AddCouponModal,
    AlertModal,
    CompleteModal
  },
  data() {
    return {    
      screenHeight: 0,        
      cellItems: [],
      canvas: null,
      context: null,
      useCouponNo: false,
      openAddCoupon: false,
      alertMsg: null,
      currPinPointObj: null
    }
  },
  mounted() {    
    this.$store.dispatch('loadCoupons')
    this.$nextTick(async()=>{
      this.setContainer() 
      this.screenHeight = window.innerHeight
      const canvasWidth = this.screenHeight * 0.7
      const canvasHeight = canvasWidth * 1.2408            
      this.canvas = new fabric.Canvas('canvas')
      this.canvas.setWidth(canvasWidth)
      this.canvas.setHeight(canvasHeight)      
      const bgImg = await this.loadImage('/images/board.png')
      bgImg.id = 'borad-bg'
      bgImg.left = -5
      bgImg.top = -5
      bgImg.scaleToWidth(canvasWidth + 10)
      bgImg.scaleToHeight(canvasHeight + 10)
      bgImg.selectable = false      
      this.canvas.add(bgImg)               
      this.setCellItems()

      const firstPositionId = localStorage.getItem('latest_position') || 'cell-1'

      const firstItem = this.findObjectById(firstPositionId)
      const pinSize = (canvasWidth / 10) - 10
      const pinImg = await this.loadImage('/images/pin.png')      
      pinImg.id = 'pin'
      pinImg.left = firstItem.left + ((firstItem.width - pinSize) / 2)
      pinImg.top  = firstItem.top + ((firstItem.height - pinSize) / 2)
      pinImg.scaleToWidth(pinSize)
      pinImg.scaleToHeight(pinSize)
      pinImg.selectable = false
      const rowNo = firstItem.row_no
      pinImg.flipX = (rowNo % 2 === 0) ? true : false      
      this.canvas.add(pinImg) 
      this.currPinPointObj = firstItem     
      // this.canvas.on("mouse:up",(e)=>{
      //   const targetObj = e.target
      //   console.log(targetObj.id)
      //   this.pinMove(targetObj.id)
      // })         
    })
  },
  methods: {    
    loadImage(src) {
      return new Promise((resolve, reject)=>{
        fabric.Image.fromURL(src, function(img) {
          resolve(img)
        })
      })
    },
    setContainer() {
      const screenHeight = window.innerHeight
      this.containerSize = screenHeight * 0.8
      this.containerMargin = (screenHeight * 0.2) / 2
    },
    setCellItems() {   
      let dataRows = []   
      const maxLine = 10
      let minNumber = 0   
      const triggerPoints = {
        'cell-4': 'cell-16',
        'cell-8': 'cell-12',
        'cell-18':'cell-38',
        'cell-20': 'cell-74',
        'cell-24':'cell-36',
        'cell-32':'cell-56',
        'cell-32':'cell-56',
        'cell-48': 'cell-54',
        'cell-40': 'cell-60',
        'cell-70': 'cell-88',
        'cell-76': 'cell-86',
        'cell-80': 'cell-100',
        'cell-90': 'cell-92',
        
        'cell-22': 'cell-2',
        'cell-30': 'cell-10',
        'cell-28': 'cell-6',
        'cell-44': 'cell-26',
        'cell-58': 'cell-42',
        'cell-68': 'cell-52',
        'cell-66': 'cell-14',
        'cell-72': 'cell-50',
        'cell-84': 'cell-62',
        'cell-94': 'cell-64',
        'cell-96': 'cell-82',
        'cell-98': 'cell-78',

      }   
      for(let i=1; i<=maxLine; i++){
        let data = []
        for(let j=1; j<=10; j++) {
          minNumber+=1
          data.push(minNumber)
        }
        if(i%2 === 0){
          data.reverse()
        }        
        dataRows.push(data)
      }

      dataRows = dataRows.reverse()
      const itemWidth = this.canvas.width / 10       
      const itemHeight = this.canvas.height / 10                      
      for(let i=0; i<dataRows.length; i++){
        const dataRow = dataRows[i]
        for(let j=0; j<dataRow.length; j++){ 
          const rectData = {
            id: `cell-${dataRow[j]}`,
            left: j * (itemWidth),
            top: i * (itemHeight),
            fill: 'transparent',            
            width: itemWidth - 2,
            height: itemHeight - 2,
            selectable: false,
            row_no: i,
            col_no: j
          }
          rectData.trigger = triggerPoints[rectData.id] || null
          this.canvas.add(new fabric.Rect(rectData))
        }
      }             
    },    
    findObjectById(id) {
      let resultObj = null
      const objects = this.canvas.getObjects()
      for(let i=0; i<objects.length; i++){
        const obj = objects[i]
        if(obj.id == id){          
          resultObj = obj
          break
        }
      }
      return resultObj
    },
    pinMove(targetId) {
      const pin = this.findObjectById('pin')          
      const targetObj = this.findObjectById(targetId)      
      let moveY = targetObj.top + ((targetObj.height - pin.getScaledHeight()) / 2)
      let moveX = targetObj.left + ((targetObj.width - pin.getScaledWidth()) / 2)
      const rowNo = targetObj.row_no
      pin.set('flipX', (rowNo % 2 === 0) ? true : false)
      this.canvas.renderAll()
      pin.animate({top: moveY, left: moveX}, {
        onChange: this.canvas.renderAll.bind(this.canvas),
        duration: 500,
        easing: fabric.util.ease.easeOutQuad,
        onComplete: ()=>{                              
          this.currPinPointObj = targetObj
          if(targetObj.trigger){
            this.pinMove(targetObj.trigger)
          }          
        }
      })
    },
    onDicePlayCompleted(diceResult){      
      const currPinPointNum = this.currPinPointObj.id.split('-')[1]
      let nextPinPointNum = Number(currPinPointNum) + diceResult      
      if(nextPinPointNum > 100) nextPinPointNum = 100
      this.pinMove(`cell-${nextPinPointNum}`)
    },
    onClickPlayDice() {
      if(this.getAvailableDiceCnt == 0){
        this.alertMsg = '<span class="text-danger"><i class="mdi mdi-information"></i> 주사위 사용횟수가 부족합니다</span>'
        return
      }
      const targetCoupon = this.getAvailableCoupons[this.getAvailableCoupons.length - 1]
      this.useCouponNo = targetCoupon.id
    },
    onCompleteMission() {
      this.pinMove('cell-1')
    }
  },
  computed: {
    getAvailableDiceCnt(){
      return this.getAvailableCoupons.length
    },
    getAvailableCoupons() {
      let coupons = this.$store.getters['getCoupons']
      coupons = Object.values(coupons).reduce((acc, item)=>{        
        if(item.count < 1) return acc
        acc.push(item)
        return acc
      }, [])
      coupons.sort((a, b)=> a.created_at < b.created_at ? 1 : -1)            
      return coupons
    },
    isMissionComplete() {
      if(!this.currPinPointObj) return
      return this.currPinPointObj.id == 'cell-100'
    }
  },
  watch: {
    currPinPointObj(value) {      
      if(value){
        localStorage.setItem('latest_position', value.id)
      }
    }
  }
}
</script>

